<template>
	<div>
		<h1>2024年信息汇总查询</h1>
		<el-card shadow="always" class="cardFa" :body-style="{ padding: '30px' }">
			<el-form ref="form" :model="PersonsData" label-width="70px" size="min">
				<el-form-item label="名字">
					<el-input v-model="PersonsData.name"></el-input>
				</el-form-item>
				<el-form-item label="学号">
					<el-input v-model.number="PersonsData.students"></el-input>
				</el-form-item>
				<el-form-item label="证件号码">
					<el-input v-model="PersonsData.major"></el-input>
				</el-form-item>
				<el-form-item label="第一门">
					<el-input v-model="PersonsData.sx"></el-input>
				</el-form-item>
				<el-form-item label="第二门">
					<el-input v-model="PersonsData.yy"></el-input>
				</el-form-item>
				<el-form-item label="第三门">
					<el-input v-model="PersonsData.yw"></el-input>
				</el-form-item>
				<el-form-item label="第四门">
					<el-input v-model="PersonsData.ty"></el-input>
				</el-form-item>
				<el-form-item label="成绩">
					<el-select v-model="PersonsData.number" placeholder="请选择第几次">
						<el-option v-for="(data, i) in optionsData" :key="i" :label="data" :value="i + 1"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="submitA">立即保存</el-button>
					<el-button @click="returnA">返回</el-button>
				</el-form-item>
			</el-form>
		</el-card>
	</div>
</template>

<script>
export default {
	name: "register",
	data() {
		return {
			PersonsData: {
				name: "", // 姓名
				students: "", //学号
				major: "", //专业
				sx: "", //数学
				yy: "", //英语
				yw: "", //因为
				ty: "", //体育
				number: "", // 第几次
			},
			optionsData: ["第一次", "第二次", "第三次",'第四次','第五次'],
			dataMax: [],
			newData: [],
			object: {},
		};
	},
	methods: {
		// 保存
		submitA() {
			for (const key in this.PersonsData) if (["", undefined, null].includes(this.PersonsData[key])) return alert(key + ":不能为空");
			let students = this.PersonsData.students;
			if (!this.object[students]) this.object[students] = [];
			let current = this.PersonsData.number - 1;
			let fixedContent = {
				name: this.PersonsData.name,
				major: this.PersonsData.major,
				sx: this.PersonsData.sx,
				yy: this.PersonsData.yy,
				yw: this.PersonsData.yw,
				ty: this.PersonsData.ty,
			};
			this.object[students][current] = fixedContent;
			localStorage.setItem("Students", JSON.stringify(this.object));
			
		},
		// 返回
		returnA(){
			this.$router.push({path:'/login'})
		}
	},
};
</script>

